@import url(https://fonts.googleapis.com/css?family=Asap+Condensed:700|Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Asap+Condensed:700|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu-card {
    float: left;
    justify-content: center;
    padding: 10px;
    margin: 10px;
    border: 2px;
    height: 300px;
    width: 250px;
    border-radius: 5px;
    box-shadow: -5px 7px 13px rgb(116, 113, 113);
}

.image {
    width: 150px;
    align-content: center;
    float: center;
    align-items: center;
}

.name{
    font-family: 'Asap Condensed', sans-serif;
    font-size: 20px;
    color: #65382e;
}

.price {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 18px;
    color: #65382e;
}
.menu-breakfast {
    display: flex;
    border: 5px;
    flex-direction: column;
}

.menu-lunch {
    display: flex;
    border: 5px;
    flex-direction: column;
}

.order {
    display:flex;
    border: 5px;
}

.lunch {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 25px;
    color: #65382e;
}

.breakfast {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 25px;
    color: #65382e;
}
*{-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out;}

.nav {
  clear:both;
  overflow:auto;
  background-color: #424242;
  padding: 10px;
}

nav {
    float:right;
}

.logo img {
    float:left;
}

ul li {
  display: inline-block; 
  padding:15px;
}

.link {
  color: white;
  text-decoration: none;
  font-family: 'Asap Condensed', sans-serif;
  font-size: 35px;
}

.link:hover {
  color:orange;
}
